import React , { Component } from 'react'
import {Form , Input , Row , Col , Select , Cascader , Button , DatePicker , Checkbox} from 'antd'

import ImgUpload from './imgUpload';

const Option = Select.Option;
const { RangePicker } = DatePicker;
const {TextArea} = Input;
const CheckboxGroup = Checkbox.Group;

class AddPageTemplate extends Component{

    constructor(props){
        super(props);
        this.state={
            loading: false
        }
    };

    template = () =>{
        const { getFieldDecorator } = this.props.form;
        return this.props.param.map((item , index)=>{
            return(
                <div key={index}>
                    <p className='addTitle'>{item.title}</p>
                    {
                        item.rows.map((item , index)=>{
                            if(item.custom){
                                return(<item.component key={index}></item.component>)
                            }else{
                                const assemble = this.switchAssembly(item);
                                return(
                                    <Row key={index}>
                                        <Col span={item.span[0]} className='addLabel'>{item.label}</Col>
                                        <Col span={item.span[1]}>
                                            <Form.Item>
                                                {getFieldDecorator(item.ref , {
                                                    initialValue: item.value,
                                                })(assemble)}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                )
                            }
                            
                        })
                    }
                </div>
            )
        });
    }

    switchAssembly = (item) =>{
        if(item.type === 'input'){
            return(<Input placeholder={item.placeholder}/>)
        }else if(item.type === 'select'){
            return(
                <Select 
                    placeholder='请选择开户邮件接收方'
                    showSearch={item.showSearch? true : false}
                >
                    {item.options ? item.options.map((item , index)=>{
                        return(<Option value={item.value} key={index}>{item.text}</Option>)
                    }):''}
                </Select>
            )
        }else if(item.type === 'cascader'){
            return(
                <Cascader 
                    options={item.options} 
                    placeholder={item.placeholder} 
                    loadData={item.loadData}
                    changeOnSelect
                />
            )
        }else if(item.type === 'imgUpload'){
            const cosSpan = 24 / item.uploadBoxs.length
            return(
                <Row>
                    {item.uploadBoxs.map((uploadItem , index)=>{
                        return(<Col span={cosSpan} key={index}><ImgUpload uploadItem={uploadItem}/></Col>)
                    })}
                </Row>
            );
            
        }else if(item.type === 'rangePicker'){
            return(<RangePicker size='default' style={{width:'100%'}}/>)
        }else if(item.type === 'textarea'){
            return(<TextArea placeholder={item.placeholder} autosize />)
        }else if(item.type === 'checkbox'){
            if(item.group){
                return(
                    <CheckboxGroup options={item.options} onChange={item.onChange} />
                );
            }
        }else {
            return(<div></div>);
        }
    }

    loadData=(selectedOptions)=>{
        console.log(selectedOptions);
    }

    render(){

        return(
            <Form>
                {this.template()}
                <Button type='primary' >提交</Button>
            </Form>
        )

    }
}

export default AddPageTemplate = Form.create()(AddPageTemplate);