import React , { Component } from 'react'

import { Card , Row , Col , Icon , Button} from 'antd'

import TableComponent from '../components/table'
import SearchComponent from '../components/search'

const searchOption = {
    param:[
        {type:'select' , placeholder:'激活状态' , ref:'activation' , options:[
            {value:'全部',text:'全部'},
            {value:'未激活',text:'未激活'},
            {value:'已激活',text:'已激活'}
        ]},
        {type:'select' , placeholder:'审核状态' , ref:'audit' , options:[
            {value:'全部',text:'全部'},
            {value:'审核中',text:'审核中'},
            {value:'审核通过',text:'审核通过'},
            {value:'审核驳回',text:'审核驳回'}
        ]},
        {type:'input' , placeholder:'所属商户名称' , ref:'merchantName'},
        {type:'input' , placeholder:'所属商户编号' , ref:'merchantNo'},
        {type:'input' , placeholder:'门店简称' , ref:'name'},
        {type:'input' , placeholder:'门店编号' , ref:'no'},
        {type:'rangePicker' , ref:'time'},
    ]
}

class ChainStorePurchase extends Component{
    componentDidMount(){
        document.title = "连锁商户进件";
    }

    constructor(){
        super();
        this.state = {
            table:{
                columns:[
                    {title:'商户编号' , dataIndex:'no'},
                    {title:'商户名称' , dataIndex:'name'},
                    {title:'商户简称' , dataIndex:'jc'},
                    {title:'激活状态' , dataIndex:'activation'},
                    {title:'审核状态' , dataIndex:'audit'},
                ]
            }
        }
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <SearchComponent param={searchOption} />
                    <TableComponent tableData={this.state.table}/>

                    <Row style={{marginTop:'15px'}}>
                        <Col span={24}>
                            <Button type='primary' style={{marginRight:'10px'}}><Icon type="plus" />新增</Button>
                            <Button type='primary'><Icon type="download" />批量导入</Button>
                        </Col>
                    </Row>
                </Card>
            </div>
        );
    }
}

export default ChainStorePurchase;