import React , { Component } from 'react'
import {Input , Row , Col , Button , Icon , DatePicker , Select , Form} from 'antd'

const { RangePicker } = DatePicker;
const Option = Select.Option;

class Search extends Component{
    constructor(props){
        super(props);
        this.state = {};
    }

    handleChange = ()=>{}

    searchItem = () =>{
        const { getFieldDecorator } = this.props.form;
        var param = this.props.param.param;
        let inputs = param.map((item , index)=>{
            switch(item.type){
                case 'select':
                    return(
                        <Col key={index} span={4} >
                            <Form.Item>
                                {getFieldDecorator(item.ref , {})(
                                    <Select
                                        showSearch={item.showSearch ? true : false}
                                        style={{width:'100%'}}
                                        placeholder={item.placeholder}
                                        onchange={()=>{
                                            
                                        }}
                                    >
                                        {item.options.map((oItem , oIndex)=>{
                                            return(<Option key={index} value={oItem.value}>{oItem.text}</Option>)
                                        })}
                                    </Select>
                                )}
                                
                            </Form.Item>
                        </Col>
                    )
                    break;
                case 'input':
                    return(
                        <Col key={index} span={4}>
                            <Form.Item>
                                {getFieldDecorator(item.ref , {})(
                                    <Input placeholder={item.placeholder} />
                                )}
                            </Form.Item>
                        </Col>
                    )
                    break;
                case 'rangePicker':
                    return(
                        <Col key={index} span={8}>
                            <Form.Item>
                                {getFieldDecorator(item.ref , {})(
                                    <RangePicker size='default' style={{width:'100%'}}/>
                                )}
                            </Form.Item>
                        </Col>
                    )
                    break;
                default : 
                    return [];
            }
        });
        return inputs;
    }

    buttons=()=>{
        if(this.props.param.buttons){
            return this.props.param.buttons.map((item , index)=>{
                return (<Col span={2} key={index}><Button type='primary' onClick={this.onClick.bind(this , item)}><Icon type={item.icon} />{item.text}</Button></Col>)
            })
        }
    }

    onClick=(item , e)=>{
        this.props.form.validateFields((err , values)=>{
            item.fn(values)
        });
    }

    searchFn=(e)=>{
        e.preventDefault();
        this.props.form.validateFields((err , values)=>{
            this.props.searchFn(values);
        });
    }

    render(){
        return(
            <div className='searchBox'>
                <Form onSubmit={this.searchFn} layout='vertical'>
                    <Row gutter={5}>
                        {this.searchItem()}
                        <Col span={2}><Button type='primary' htmlType='submit'><Icon type='search'></Icon>搜索</Button></Col>
                        {this.buttons()}
                    </Row>
                </Form>
            </div>
        );
    }
}

export default Search = Form.create()(Search);