import React , { Component } from 'react'
import { Card , Drawer , Row , Col} from 'antd'

import Search from '../components/search'
import TableComponent from '../components/table'

const pStyle = {
    fontSize: 16,
    color: 'rgba(0,0,0,0.85)',
    lineHeight: '24px',
    display: 'block',
    marginBottom: 16,
};

const DescriptionItem = ({ title, content }) => (
    <div
      style={{
        fontSize: 14,
        lineHeight: '22px',
        marginBottom: 7,
        color: 'rgba(0,0,0,0.65)',
      }}
    >
      <p
        style={{
          marginRight: 8,
          display: 'inline-block',
          color: 'rgba(0,0,0,0.85)',
        }}
      >
        {title}:
      </p>
      {content}
    </div>
);

const searchOption = {
    param:[
        {type:'rangePicker' , ref:'time'},
        {type:'select' , placeholder:'规划状态' , ref:'state' , options:[
            {value:'全部',text:'全部'},
            {value:'等待规划',text:'等待规划'},
            {value:'规划成功',text:'规划成功'},
            {value:'规划失败',text:'规划失败'},
            {value:'退汇',text:'退汇'}
        ]},
        {type:'select' , placeholder:'支付渠道' , ref:'pay' , options:[
            {value:'全部',text:'全部'},
            {value:'微信',text:'微信'},
            {value:'支付宝',text:'支付宝'},
            {value:'京东支付',text:'京东支付'},
            {value:'QQ钱包',text:'QQ钱包'}
        ]},
        
    ],
    buttons:[
        {icon:'export',text:'导出',fn:(json)=>{console.log(json)}},
    ]
}

class ChannelSettlement extends Component{
    constructor(props){
        super(props);
        this.state={
            detailData:null,
            searchData:null,
            visibel: false,
            table: {
                columns:[{
                    title: '结算日期',
                    dataIndex: 'jstime',
                    width: '120px'
                }, {
                    title: '交易日期',
                    dataIndex: 'jytime'
                }, {
                    title: '结算户名',
                    dataIndex: 'huming'
                },  {
                    title: '支付渠道',
                    dataIndex: 'qudao'
                },  {
                    title: '结算账号',
                    dataIndex: 'account'
                }, ,  {
                    title: '金额',
                    dataIndex: 'amount'
                }, {
                    title: '状态',
                    dataIndex: 'state'
                }, {
                    title: '操作',
                    dataIndex: 'option',
                    render:(text, record, index)=>(<a href="javascript:void(0)" onClick={()=>{
                        this.detail(record);
                    }}>详情</a>)
                }],
                data:[{
                    jstime: '2019-02-11',
                    jytime: '2019-02-10',
                    name: '营山县全名副食店',
                    jiancheng: '营山县全名副食店',
                    no: '100001260863',
                    huming: '罗伶俐',
                    qudao: '微信',
                    account: '623185009200047766',
                    amount: 14.95,
                    state:'划付成功',
                    detail: '行内对私转账成功', 
                    bank: '(香港地区)上海银行（香港）有限公司',
                    bankNo: '325290000012',
                    key:1
                }]
            },
        }
    }

    detail = (record) =>{
        this.setState({
            detailData: record,
            visibel: true,
        });
    }

    onClose = () =>{
        this.setState({
            visibel:false
        })
    }

    searchFn = (json)=>{
        this.setState({
            searchData:json
        })
    }

    componentDidMount(){
        document.title = "渠道结算";
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <Search param={searchOption} searchFn={this.searchFn}/>
                    <TableComponent tableData={this.state.table}/>
                </Card>

                <Drawer
                    width={400}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    {this.state.detailData ? (
                        <div>
                            <p style={{ ...pStyle , marginBottom: 24 }}>商户信息</p>
                            <Row><Col><DescriptionItem title="清算日期" content={this.state.detailData.jstime} /></Col></Row>
                            <Row><Col><DescriptionItem title="交易日期" content={this.state.detailData.jytime} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户名称" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户简称" content={this.state.detailData.jiancheng} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户编号" content={this.state.detailData.no} /></Col></Row>
                            <Row><Col><DescriptionItem title="金额" content={this.state.detailData.amount} /></Col></Row>
                            <Row><Col><DescriptionItem title="支付渠道" content={this.state.detailData.qudao} /></Col></Row>
                            <Row><Col><DescriptionItem title="状态" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="详情" content={this.state.detailData.state} /></Col></Row>
                            <p style={{ ...pStyle , marginBottom: 24 , marginTop: 24 }}>结算信息</p>
                            <Row><Col><DescriptionItem title="结算用户" content={this.state.detailData.huming} /></Col></Row>
                            <Row><Col><DescriptionItem title="结算银行" content={this.state.detailData.bank} /></Col></Row>
                            <Row><Col><DescriptionItem title="支行联行号" content={this.state.detailData.bankNo} /></Col></Row>
                            <Row><Col><DescriptionItem title="结算账号" content={this.state.detailData.account} /></Col></Row>
                        </div>
                    ) : ''}
                </Drawer>
            </div>
        )
    }
}

export default ChannelSettlement;