import React , { Component } from 'react';
import { Table , Input } from 'antd'

const columns = [
    {title:'支付类型' , dataIndex:'payType'},
    {title:'费率' , dataIndex:'rate',render:(text, record, index)=>{
        if(record.editable){
            return(
                <Input placeholder="请输入费率" defaultValue={text} ref={record.key}/>
            )
        }else{
            return(
                <div>{text}</div>
            )
        }
        
    }}
]

const dataSource = [
    {key:'0' , payType:'扫码支付' , rate:''},
    {key:'1' , payType:'刷卡支付' , rate:''},
    {key:'2' , payType:'公众号支付' , rate:''}
]

class Editable extends Component{

    constructor(props){
        super(props);
        this.state={
            dataSource:dataSource
        }
    }

    render(){
        return(
            <Table
                columns={columns}
                dataSource={this.state.dataSource}
                bordered
                rowSelection={{
                    columnTitle:'选择',
                    fixed:true,
                    hideDefaultSelections:true,
                    type:'checkbox',
                    onSelect:(record, selected, selectedRows)=>{
                        var key = record.key;
                        let {dataSource} = this.state;
                        if(selected){
                            dataSource[key].editable = true;
                        }else{
                            dataSource[key].editable = false;
                        }
                    }
                }}
                pagination={false}
            ></Table>
        )
    }
}

export default Editable;