import React , { Component } from 'react'

import logo from '../assets/images/logo.jpg'
import logoSmall from '../assets/images/logo_small.jpg'

import {
    Menu,
    Icon
} from 'antd'
import SubMenu from 'antd/lib/menu/SubMenu';

import {Link} from 'react-router-dom';

import testData from '../assets/testData/menu'

class MenuConpont extends Component{
    constructor(props){
        super(props);
        this.state = {
            logo:{'display':'block'},
            logoSmall:{'display':'none'},
            menus:testData,
            collapsed:this.props.flag,
            defaultSelectedKeys:['0']
        }
    }

    clickMenu =(item , parentItem) => {
        this.props.updateTags(item , parentItem);
    }

    selectedKey = (key)=>{
        var arr = new Array();
        arr.push(key+'');
        this.setState({
            defaultSelectedKeys:arr
        });
    }

    forMenu = (callback) => {
        var that = this;
        var arr = this.state.menus;
        let isValidArr = value => value && Array.isArray(value);
        let isValidArrChild = value => value && value.subMenu && Array.isArray(value.subMenu) && value.subMenu.length > 0;
        function recursive(arr , parentItem){
            if(isValidArr(arr)){
                
                return arr.map((item , index)=>{
                    if(isValidArrChild(item)){
                        return(
                            <SubMenu key={item.id} title={
                                <span>
                                    <Icon type={item.icon} />
                                    <span>{item.label}</span>
                                </span>
                            }>
                                {recursive(item.subMenu , item)}
                            </SubMenu>
                        )
                    }else{
                        return(
                            <Menu.Item key={item.id} onClick={that.clickMenu.bind(this , item , parentItem)}>
                                <Link to={{ pathname: item.link }} replace>{item.label}</Link> 
                            </Menu.Item>
                        );
                    }
                });
            }
        }
        return recursive(arr);
    }

    logo = () =>{
        if(!this.props.flag){
            return <img src={logo}/>;
        }else{
            return <img src={logoSmall}/>
        }
    }

    render(){
        return(
            <div className='menuBox'>
                <div className='side-menu'>
                    <div className='logo-con'>
                        
                        {this.logo()}
                    </div>
                    <Menu 
                        selectedKeys={this.state.defaultSelectedKeys}
                        theme='dark'
                        mode='inline'
                        inlineCollapsed={this.props.flag}
                    >
                        {this.forMenu()}
                        
                    </Menu>
                </div>
            </div>
        )
    }
}

export default MenuConpont;