import React , { Component } from 'react'

import { Card , Row , Col , Button , Icon , Drawer} from 'antd'

import TableComponent from '../components/table'
import SearchComponent from '../components/search'
import OrdinaryMerchantAdd from './ordinaryMerchantAdd'

const searchOption = {
    param:[
        {type:'select' , placeholder:'激活状态' , ref:'activation' , options:[
            {value:'全部',text:'全部'},
            {value:'未激活',text:'未激活'},
            {value:'已激活',text:'已激活'}
        ]},
        {type:'select' , placeholder:'审核状态' , ref:'audit' , options:[
            {value:'全部',text:'全部'},
            {value:'审核中',text:'审核中'},
            {value:'审核通过',text:'审核通过'},
            {value:'审核驳回',text:'审核驳回'}
        ]},
        {type:'input' , placeholder:'商户名称' , ref:'name'},
        {type:'input' , placeholder:'商户编号' , ref:'no'},
        {type:'rangePicker' , ref:'time'},
        {type:'select' , placeholder:'业务员' , ref:'salesman' , options:[] , showSearch:true},
    ],
    buttons:[
        {text:'导出' , icon:'upload'}
    ]
}

class OrdinaryMerchantPurchase extends Component{
    componentDidMount(){
        document.title = "普通商户进件";
    }

    constructor(){
        super();
        this.state = {
            table:{
                columns:[
                    {title:'商户编号' , dataIndex:'no'},
                    {title:'商户名称' , dataIndex:'name'},
                    {title:'商户简称' , dataIndex:'jc'},
                    {title:'激活状态' , dataIndex:'activation',width:'120px'},
                    {title:'审核状态' , dataIndex:'audit',width:'120px'},
                ]
            },
            visibel:false
        }
    }

    onClose=()=>{
        this.setState({visibel:false})
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <SearchComponent param={searchOption} />
                    <TableComponent tableData={this.state.table}/>

                    <Row style={{marginTop:'15px'}}>
                        <Col span={24}>
                            <Button type='primary' style={{marginRight:'10px'}} onClick={()=>{this.setState({visibel:true})}}><Icon type="plus" />新增</Button>
                            <Button type='primary'><Icon type="download" />批量导入</Button>
                        </Col>
                    </Row>
                </Card>
                <Drawer
                    width={600}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    <OrdinaryMerchantAdd />
                </Drawer>
            </div>
        );
    }
}

export default OrdinaryMerchantPurchase;