import React , { Component } from 'react'
import { Card , Drawer , Row , Col} from 'antd'

import Search from '../components/search'
import TableComponent from '../components/table'

const pStyle = {
    fontSize: 16,
    color: 'rgba(0,0,0,0.85)',
    lineHeight: '24px',
    display: 'block',
    marginBottom: 16,
};

const DescriptionItem = ({ title, content }) => (
    <div
      style={{
        fontSize: 14,
        lineHeight: '22px',
        marginBottom: 7,
        color: 'rgba(0,0,0,0.65)',
      }}
    >
      <p
        style={{
          marginRight: 8,
          display: 'inline-block',
          color: 'rgba(0,0,0,0.85)',
        }}
      >
        {title}:
      </p>
      {content}
    </div>
);

const searchOption = {
    param:[
        {type:'rangePicker' , ref:'time'},
        {type:'select' , placeholder:'支付渠道' , ref:'qudao' , options:[
            {value:'全部',text:'全部'},
            {value:'微信',text:'微信'},
            {value:'支付宝',text:'支付宝'},
            {value:'京东支付',text:'京东支付'},
            {value:'QQ钱包',text:'QQ钱包'}
        ]},
        {type:'select' , placeholder:'退款状态' , ref:'state' , options:[
            {value:'全部',text:'全部'},
            {value:'退款成功',text:'退款成功'},
            {value:'退款失败',text:'退款失败'},
            {value:'退款处理中',text:'退款处理中'},
            {value:'转入代发',text:'转入代发'}
        ]},
        {type:'input' , placeholder:'商户名称' , ref:'name'},
        {type:'input' , placeholder:'商户编号' , ref:'no'},
        {type:'input' , placeholder:'商户/门店简称' , ref:'jiancheng'},
        {type:'input' , placeholder:'门店编号' , ref:'storeNo'},
        {type:'input' , placeholder:'uline交易号' , ref:'uline'},
        {type:'input' , placeholder:'第三方交易号' , ref:'transNo'},
        {type:'input' , placeholder:'商户交易号' , ref:'merchantNo'},
        {type:'input' , placeholder:'uline对单号' , ref:'refundUlineNo'},
        {type:'input' , placeholder:'第三方对单号' , ref:'refundNo'},
        {type:'input' , placeholder:'商户退单号' , ref:'merchantRefundNo'},
    ],
    buttons:[
        {icon:'export',text:'导出',fn:(json)=>{console.log(json)}},
    ]
}

class ErchantTransaction extends Component{
    constructor(props){
        super(props);
        this.columns=[{
            title: '支付日期',
            dataIndex: 'paytime',
            width: 130
        }, {
            title: 'uline交易号 / 商户交易号',
            width: 180,
            dataIndex: 'no',
        }, {
            title: 'uline退款单号 / 第三方退款单号',
            width: 230,
            dataIndex: 'no2',
        }, {
            title: '商户名称',
            dataIndex: 'name',
        }, {
            title: '商户简称',
            dataIndex: 'jiancheng',
        }, {
            title: '支付渠道',
            width: 70,
            dataIndex: 'qudao',
        }, {
            title: '退款金额',
            width: 70,
            dataIndex: 'amount',
        }, {
            title: '当前状态',
            width: 50,
            dataIndex: 'state',
        }, {
            title: '操作',
            dataIndex: 'option',
            width: 50,
            render: (text, record, index)=>(
                <a href="javascript:void(0)" onClick={()=>{
                    this.detail(record);
                }}>详情</a>
            )
            
        }]

        this.state={
            detailData:null,
            searchData:null,
            visibel: false,
            dataSource: [{
                createtime: '2019-09-09',
                paytime: '2019-02-11',
                uline: '41000021394761272264383119101952',
                no: '410000213947... wxpay201902251...',
                no2: '410000213947... wxpay20190225...',
                name: '北京同仁堂重庆小什字药店',
                jiancheng: '北京同仁堂重庆小什字药店',
                qudao: '微信',
                paytype: '公众号支付',
                amount: 14.95,
                state:'支付成功',
                sfno:'4200000275201902259180838863',
                payno:'wxpay201902251641065269lZUOPRIV',
                merchant:'成都英黎科技有限公司',
                key:1
            }]
        }
    }

    detail = (record) =>{
        this.setState({
            detailData: record,
            visibel: true,
        });
    }

    onClose = () =>{
        this.setState({
            visibel:false
        })
    }

    searchFn = (json)=>{
        this.setState({
            searchData:json
        })
    }

    componentDidMount(){
        document.title = "商户退款";
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <Search param={searchOption} searchFn={this.searchFn}/>
                    <TableComponent tableData={{columns:this.columns , data:this.state.dataSource}}/>
                </Card>
                <Drawer
                    width={400}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    {this.state.detailData ? (
                        <div>
                            <p style={{ ...pStyle , marginBottom: 24 }}>单号详情</p>
                            <Row><Col><DescriptionItem title="uline交易号" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="第三方交易号" content={this.state.detailData.sfno} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户交易号" content={this.state.detailData.payno} /></Col></Row>
                            <Row><Col><DescriptionItem title="uline退单号" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="第三方退单号" content={this.state.detailData.sfno} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户退单号" content={this.state.detailData.payno} /></Col></Row>
                            <p style={{ ...pStyle , marginBottom: 24 , marginTop: 24 }}>商户详情</p>
                            <Row><Col><DescriptionItem title="商户名称" content={this.state.detailData.name} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户简称" content={this.state.detailData.jiancheng} /></Col></Row>
                            <Row><Col><DescriptionItem title="商户编号" content={this.state.detailData.jiancheng} /></Col></Row>
                            <Row><Col><DescriptionItem title="所属渠道商" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="所属渠道商编号" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="第三方商户号" content={this.state.detailData.state} /></Col></Row>
                            <Row><Col><DescriptionItem title="支付类型" content={this.state.detailData.paytype} /></Col></Row>
                            <Row><Col><DescriptionItem title="交易金额" content={this.state.detailData.amount} /></Col></Row>
                            <Row><Col><DescriptionItem title="退款金额" content={this.state.detailData.amount} /></Col></Row>
                            <Row><Col><DescriptionItem title="对账状态" content={this.state.detailData.state}/></Col></Row>
                            <p style={{ ...pStyle , marginBottom: 24 , marginTop: 24 }}>退单状态</p>
                            <Row><Col><DescriptionItem title="变更时间" content={this.state.detailData.createtime} /></Col></Row>
                            <Row><Col><DescriptionItem title="状态" content={this.state.detailData.state} /></Col></Row>
                        </div>
                    ) : ''}
                </Drawer>
            </div>
        )
    }
}

export default ErchantTransaction;