import React , { Component } from 'react'
import MenuComponent from '../components/menu'
import '../assets/css/main.css'

import {Icon , Breadcrumb , Dropdown , Menu , Drawer , Layout} from 'antd';

import {Route} from 'react-router-dom';

import Home from '../pages/home';
import MerchantSettlement from '../pages/merchantSettlement';
import ChannelSettlement from '../pages/channelSettlement';
import OrdinaryMerchantPurchase from '../pages/ordinaryMerchantPurchase';
import ChainMerchantPurchase from '../pages/chainMerchantPurchase';
import ChainStorePurchase from '../pages/chainStorePurchase';
import Statement from '../pages/statement';
import MerchantTransaction from '../pages/merchantTransaction';
import MerchantRefund from '../pages/merchantRefund';

import UserInfo from '../components/userInfo'

const {Header , Content , Sider} = Layout;

class Main extends Component{

    constructor(){
        super();
        this.state={
            leftSiderFlag : false,
            visibel: false,
            marginL:{marginLeft: '200px'},
            tags:[
                {id:0 , tagName:'首页' , isClose:false , isCheck:true}
            ],
            crumb:[
                {icon:'home',name:'首页'}
            ]
        }
    }

    updateTags = (item , parentItem) =>{
        let flag = true;
        let tags = this.state.tags;
        let crumb = this.updateCrumb(item , parentItem);
        let link = item ? item.link : '/main/home'
        this.props.history.push({
            pathname:link
        });
        if(item){
            this.refs.menu.selectedKey(item.id);
        }else{
            this.refs.menu.selectedKey(0);
            item = this.state.tags[0];
        }
        
        for(let i = 0;i < tags.length;i++){
            if(tags[i].id === item.id){
                tags[i].isCheck = true;
                flag = false;
            }else{
                tags[i].isCheck = false;
            }
        }

        if(flag){
            tags.push({id:item.id,tagName:item.label,isClose:true,isCheck:true,item:item,parentItem:parentItem});
        }

        this.setState({
            tags:tags,
            crumb:crumb
        });
    }

    updateCrumb = (item , parentItem) =>{ 
        let crumb = new Array();
        crumb.push({icon:'home',name:'首页'});
        if(parentItem){crumb.push({icon:parentItem.icon,name:parentItem.label});}
        if(item){crumb.push({icon:item.icon,name:item.label});}
        return crumb
    }

    updateBar = () =>{
        var ml = {marginLeft: '200px'};
        if(!this.state.leftSiderFlag){
            ml = {marginLeft: '80px'};
        }
        this.setState({
            leftSiderFlag: !this.state.leftSiderFlag,
            marginL: ml
        });
    }

    closeTag = (key , e) =>{
        e.stopPropagation();
        let tags = this.state.tags;
        let isHave = false;
        let removeNum = null;
        for(let i = 0;i < tags.length;i++){
            if(tags[i].id === key){
                removeNum = i;
                if(tags[i].isCheck){isHave = true}
            }
        }
        var crumb;
        if(isHave){
            tags[removeNum-1].isCheck = true;this.refs.menu.selectedKey(tags[removeNum-1].id);
            crumb = this.updateCrumb(tags[removeNum-1].item , tags[removeNum-1].parentItem);
            var link = tags[removeNum-1].item ? tags[removeNum-1].item.link : '/main/home';
            this.props.history.push({
                pathname:link
            });
        }else{
            crumb = this.updateCrumb(tags[removeNum-1].item , tags[removeNum-1].parentItem);
        }
        tags.splice(removeNum , 1);
        this.setState({
            tags:tags,
            crumb: crumb
        });
    }

    showDrawer = () =>{
        this.setState({
            visibel:true
        })
    }

    onClose = () =>{
        this.setState({
            visibel:false
        })
    }
    
    render(){
        var nav = (
            <Menu>
                <Menu.Item onClick={this.showDrawer}>
                    <span>用户信息</span>
                </Menu.Item>
                <Menu.Item>
                    <span>修改密码</span>
                </Menu.Item>
                <Menu.Item>
                    <span>开发文档</span>
                </Menu.Item>
                <Menu.Item>
                    <span>下载</span>
                </Menu.Item>
                <Menu.Item>
                    <span>操作文档手册</span>
                </Menu.Item>
                <Menu.Item>
                    <span>退出</span>
                </Menu.Item>
            </Menu>        
        );

        return(
            <div className='mainBox'>
                <Layout>
                    <Sider
                        style={{overflow: 'auto', height: '100vh', position: 'fixed', left: 0}}
                        collapsed
                        collapsed={ this.state.leftSiderFlag}
                    >
                        <MenuComponent ref='menu' flag={this.state.leftSiderFlag} updateTags={this.updateTags}></MenuComponent>
                    </Sider>
                    <Layout className="rightSider" style={{...this.state.marginL}}>
                        <Header style={{background:'#fff',padding:0}}>
                            <div className='header-con'>
                                <div className='header-bar'>
                                    <a className='sider-a'>
                                        <Icon className="trigger" type={this.state.leftSiderFlag ? 'menu-unfold' : 'menu-fold'} onClick={this.updateBar} />
                                    </a>

                                    <div className='crumb'>
                                        <Breadcrumb>
                                            {this.state.crumb.map((item , index)=>{
                                                return(
                                                    <Breadcrumb.Item key={index}><Icon type={item.icon} /><span>{item.name}</span></Breadcrumb.Item>
                                                );
                                            })}
                                        </Breadcrumb>
                                    </div>
                                </div>
                                <div className='custom-con'>
                                    <Dropdown overlay={nav} placement="bottomCenter">
                                        <div className='avatar'>
                                            <img src={require('../assets/images/logo_small.jpg')} className='avatar' />
                                            <Icon type='caret-down' />
                                        </div>
                                        
                                    </Dropdown>
                                </div>
                            </div>
                        </Header>
                        <div className='tagBox'>
                            <div className='tags-nav'>
                                <div className='btn-con left-btn'><Icon type='left' /></div>
                                <div className='btn-con right-btn'><Icon type='right' /></div>
                                <div className='scroll-outer'>
                                    <div className='scroll-body'>
                                        {this.state.tags.map((item , index)=>{
                                            return(
                                                <span key={item.id} onClick={this.updateTags.bind(this , item.item , item.parentItem)}>
                                                    <div className={item.isCheck ? 'tag tag-primay' : 'tag'}>
                                                        <span className='tag-dot-inner'></span>
                                                        <span className='tag-text'>{item.tagName}</span>
                                                        <Icon type='close' onClick={this.closeTag.bind(this , item.id)} style={{marginLeft:'10px'}} className={item.isClose ? '' : 'displayNone'}/>
                                                    </div>
                                                </span>
                                            )
                                        })}
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Content style={{minHeight: 280}}>
                            <Route exact path='/main/home' component={Home}></Route>
                            <Route path='/main/merchantSettlement' component={MerchantSettlement}></Route>
                            <Route path='/main/ordinaryMerchantPurchase' component={OrdinaryMerchantPurchase}></Route>
                            <Route path='/main/chainMerchantPurchase' component={ChainMerchantPurchase}></Route>
                            <Route path='/main/chainStorePurchase' component={ChainStorePurchase}></Route>
                            <Route path='/main/statement' component={Statement}></Route>
                            <Route path='/main/channelSettlement' component={ChannelSettlement}></Route>
                            <Route path='/main/merchantTransaction' component={MerchantTransaction}></Route>
                            <Route path='/main/merchantRefund' component={MerchantRefund}></Route>
                        </Content>
                    </Layout>
                </Layout>
                
                <Drawer
                    width={320}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    <UserInfo />
                </Drawer>
            </div>
        );
    }
}

export default Main;